                <p>Adems de su valiosa labor docente, tambin persigui su crecimiento acadmico a travs de cursos de doctorado en 1984-85. Durante este tiempo, se embarc en una emocionante investigacin que culmin en su tesis doctoral titulada "Anlisis de la forma y el color en la pintura de ngel Lizcano", contribuyendo as al enriquecimiento del entendimiento artstico.</p>
